.order-confirmation {
  padding: 20px;
  max-width: 400px;
  margin: 0 auto;
  background-color: #ffe0e0;
}

.header {
  width: auto;
  display: flex;
  align-items: center;
  flex-direction: row;
  font-size: 20px;
  color: black;
}
.header .arrow {
  font-size: 25px;
  margin-right: 10px;
  cursor: pointer;
}

.title-checkout {
  font-size: 24px;
  font-weight: bold;
}

.hr {
  border: 1px solid #e0e0e0;
}

.customer-info .summary {
  margin-bottom: 20px;
}

.title-info {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.title-info p {
  font-size: 16px;
  font-weight: bold;
  margin-right: 10px;
}

.title-info img {
  background-color: #e6707a;
  margin: 0;
  width: 20px;
  height: 20px;
}

.order-items {
  display: flex;
  flex-direction: column;
}

.order-item {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 10px;
}

.pizza-img {
  width: 40px;
  height: 40px;
  margin: 0;
}

.view-all-btn {
  background-color: #c41f2d;
  border: none;
  padding: 10px;
  margin-bottom: 20px;
  cursor: pointer;
}

.summary p {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}

.payment-method {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.payment-btn {
  background-color: #7e4444;
  border: none;
  padding: 10px;
  flex: 1;
  margin: 0 5px;
  cursor: pointer;
}

.payment-btn.active {
  background-color: #ff6b6b;
  color: white;
}

.download-qr-btn {
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.download-qr-btn:hover {
  background-color: #45a049;
}

.disabled-btn {
  background-color: #3a3737;
  color: white;
  cursor: not-allowed;
}

.send-order-btn {
  background-color: #ff6b6b;
  color: white;
  border: none;
  padding: 15px;
  width: 100%;
  cursor: pointer;
}

.qr-code-image {
  width: 100%;
  height: auto;
  border-radius: 0%;
}
